import axios from 'axios';

import CreateComponent from '@/components/CreateComponent/index.vue'

export default {
  name: 'search-component',
  components: { CreateComponent },
  props: [],
  data () {
    
    return {
      //reseau: 'RER', 
      reseau: '',           
      station: '',
      trafficMin: '',
      trafficMax: '',
      ville: '',
      contextTrafficServiceUrl: 'http://54.38.186.137:9080/StationDemoWeb/station',
      resultSearchs : [],

      listeReseauxDispo: [
        { key: '', value: '-------------' },
        { key: 'Metro', value: 'Metro' },
        { key: 'RER', value: 'RER' },
      ]


    }
  },
  computed: {

  },
  mounted () {

  },
  methods: {

    sub: function(event){
 
      axios.post(this.contextTrafficServiceUrl + '/findStationsByCrit',
        {
            'reseau' : this.reseau,
            'station': this.station,
            'trafficMin': this.trafficMin,
            'trafficMax': this.trafficMax,
            'ville': this.ville,
            'numberMaxElements': 300,
            'page': 1


        },{
         headers: {
           'Content-Type': 'application/json',
        },
      })
      .then(response => {
        var data = response.data
        this.resultSearchs = data
      })
      .catch(e => {
        this.errors.push(e)
      })
      event.preventDefault();
    },


    createComponent : function(message) {
      this.$router.push('/createStation')
    },

    selectStation : function(idToSelect){
      this.$router.push('/selectStation/' + idToSelect)
    }
  }
}



